import {formatDate,formatDuration} from '../../util.js';

function createPointItself(pointModel) {

  const { pointType, destination, price, offers, startTime, endTime } = pointModel;
  const offersMarkup = createOffers(offers);

  const destinationName = destination.name;

  const eventDate = formatDate(startTime, 'MMM DD');
  const eventDateMarkup = formatDate(startTime, 'YYYY-MM-DD');

  const eventStartTimeMarkup = formatDate(startTime, 'YYYY-MM-DDTHH:mm:ss');
  const eventEndTimeMarkup = formatDate(startTime, 'YYYY-MM-DDTHH:mm:ss');

  const eventStartTime = formatDate(startTime, 'HH:mm');
  const eventEndTime = formatDate(endTime, 'HH:mm');

  const duration = formatDuration(startTime, endTime);

  return `<li class="trip-events__item">
            <div class="event">
              <time class="event__date" datetime="${eventDateMarkup}}">${eventDate}</time>
              <div class="event__type">
                <img class="event__type-icon" width="42" height="42" src="img/icons/taxi.png" alt="Event type icon">
              </div>
              <h3 class="event__title">${pointType} ${destinationName}</h3>
              <div class="event__schedule">
                <p class="event__time">
                  <time class="event__start-time" datetime="${eventStartTimeMarkup}">${eventStartTime}</time>
                  &mdash;
                  <time class="event__end-time" datetime="${eventEndTimeMarkup}">${eventEndTime}</time>
                </p>
                <p class="event__duration">${duration}</p>
              </div>
              <p class="event__price">
                &euro;&nbsp;<span class="event__price-value">${price}</span>
              </p>
              <h4 class="visually-hidden">Offers:</h4>
              <ul class="event__selected-offers">
                ${offersMarkup}
              </ul>
              <button class="event__favorite-btn event__favorite-btn--active" type="button">
                <span class="visually-hidden">Add to favorite</span>
                <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
                  <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
                </svg>
              </button>
              <button class="event__rollup-btn" type="button">
                <span class="visually-hidden">Open event</span>
              </button>
            </div>
          </li>
  `;
}

function createOffers(offers) {

  return (
    offers.map(({ name, price }) =>
      `<li class="event__offer">
        <span class="event__offer-title">${name}</span>
        &plus;&euro;&nbsp;
        <span class="event__offer-price">${price}</span>
      </li>`
    ).join(''));
}


export { createPointItself };
